import VueRouter from 'vue-router';
import Vue from 'vue'

import Login from '../components/Login.vue';
import Firebase from '../components/Firebase.vue';
import Edit from '../components/Edit.vue';
import NotFound from '../components/NotFound.vue'

Vue.use(VueRouter);

const router = new VueRouter({
    routes: [
        {
            path: "/",
            name: "login",
            component: Login
        },
        {
            path: '/app.firebase-demo',
            redirect: '/firebase-demo'
        },
        {
            path: "/firebase-demo",
            name: "firebase-demo",
            component: Firebase
        },
        {
            path: '/firebase-demo/edit/:id',
            name: 'edit',
            component: Edit
        },
        {
            path: "*",
            component: NotFound
        },
        {
            path: '/NotFound',
            component: NotFound
        }
    ]
});

export default router
