<template>
  <div class="container">
      <h1>Login</h1>

      <div class="col-md-6 offset-3">
          <form @submit.prevent="login()">
              <b-alert :show="alertShow" :variant="alertType">{{ alertMessage }}</b-alert>
              <div class="form-group">
                  <b-input placeholder="Email" id="email" type="email" v-model="form.email" name="email" required></b-input>
              </div>
              <div class="form-group">
                  <b-input placeholder="Password" id="pass" type="password" v-model="form.password" name="pass"></b-input>
              </div>

              <b-button v-if="!loginLoading" type="submit" class="btn btn-sm btn-info">Login</b-button>
              <b-button v-if="loginLoading" type="button" class="btn btn-sm btn-info" disabled><b-spinner small type="grow"></b-spinner>Login</b-button>
          </form>
      </div>

  </div>
</template>

<script>
import firebase from 'firebase'
import 'firebase/auth'
export default {
    data() {
        return {
            loginLoading: false,
            alertShow: false,
            alertType: 'success',
            alertMessage: '',
            form: {
                email: '',
                password: ''
            }
        }
    },
    methods: {
        login() {
            this.loginLoading = true;
            firebase.auth().signInWithEmailAndPassword(this.form.email, this.form.password)
                    .then( () => {

                        this.notify('success', 'Login successful!');

                        setTimeout( () => { 
                            this.$router.replace('firebase-demo') 
                        }, 2000);
                    })
                    .catch( error => {
                        // Handle errors
                        // var errorCode = error.code;
                        var errorMessage = error.message;

                        this.notify('danger', errorMessage)
                        this.loginLoading = false;
                    })
        },
        notify(type, msg) {
            this.alertShow = true;
            this.alertType = type;
            this.alertMessage = msg;
        }
    }
}
</script>

