<template>
  <div class="container">
      <h1>Firebase Demo</h1>
       <small><a href="#" @click="signout()">Sign out</a></small>

        <form @submit.prevent="saveUser()" class="mt-2">
            <input type="text" v-model="name" name="name" id="name" placeholder="Your name" required/>
            <button type="submit">Save</button>
        </form>
        <br>
        <b-alert :variant="alertType" :show="showAlert" dismissible>{{ alertMsg }}</b-alert>
        <b-table
            bordered
            striped
            :fields="columns"
            :items="users"
            :per-page="perPage"
            :current-page="currentPage"
            class="table-sm"
        >
            <template v-slot:cell(data)="data">
                {{ data.item.data.name }}
            </template>
            <template v-slot:cell(action)="data">
                <!-- <b-button @click="editUser(data.item)" class="btn btn-sm btn-info">EDIT</b-button> -->
                <router-link :to="{ name: 'edit', params: {id: data.item.id}}" class="btn btn-sm btn-info mr-2">EDIT</router-link>
                <b-button @click="removeUser(data.item)" class="btn btn-sm btn-danger">DELETE</b-button>
            </template>
        </b-table>
        <b-pagination
            align="center"
            size="sm"
            v-model="currentPage"
            :per-page="perPage"
            :total-rows="users.length"
        ></b-pagination>
  </div>
</template>

<script>
import * as firebase from 'firebase';
import 'firebase/auth';
import 'firebase/database';

export default {
    data() {
        return {
            perPage: 5,
            currentPage: 1,
            showAlert: false,
            alertType: 'success',
            alertMsg: '',
            name: '',
            users: Array(),
            columns: [
                {
                    key: 'data',
                    label: 'Name',
                    sortable: true,
                },
                { key: 'action', label: 'Action'}
            ]
        }
    },
    mounted() {
        this.authenticate();
        this.loadData();
    },
    methods: {
        authenticate() {
            firebase.auth().onAuthStateChanged( user => {
                if (user) {
                    // User is signed in.
                    // var displayName = user.displayName;
                    // var email = user.email;
                    // var emailVerified = user.emailVerified;
                    // var photoURL = user.photoURL;
                    // var isAnonymous = user.isAnonymous;
                    // var uid = user.uid;
                    // var providerData = user.providerData;
                    // ...
                    console.log('User is signed-in: ', user.email)
                } else {
                    // User is signed out.
                    // ...
                    this.$router.push('/');
                    console.log('User is signed-out');
                }
            });
        },
        signout() {
            firebase.auth().signOut()
                    .then( () => {
                        // Signed out successful
                        this.$router.push('/')
                    })
                    .catch( error => {
                        console.log('Error: ', error)
                    })
        },
        loadData() {

            var firestore = firebase.firestore();

            firestore.collection('users').orderBy('name').onSnapshot( querySnapshot => {
                this.users = [];
                    querySnapshot.forEach(data => {
                        this.users.push({ id: data.id, data: data.data()});
                    })
            })
            
        },
        validateInput() {
            var firestore = firebase.database();

            firestore.ref('user').orderByChild('name')
                        .equalTo(this.name)
                        .once('value', snapshot => {
                            if(snapshot.exist()) {
                                return false;
                            } else {
                                return true;
                            }
                        })
        },
        saveUser() {
            var firestore = firebase.firestore();

            // var docRef = firestore.doc('users');

            if(this.validateInput()) {
                firestore.collection('users').add({
                    name: this.name
                }).then( () => {
                    this.name = '';
                    
                    this.notify("success", "Data successfully saved!");
                    
                }).catch(function(error) {
                    console.log('Got an error: ', error);
                });
            } else {
                this.notify("danger", "Name already exist!")
            }
 
        },
        removeUser(data) {
            var firestore = firebase.firestore();

            firestore.collection('users').doc(data.id).delete()
                        .then( () => {
                            this.notify("success", "Name successfully removed!")
                        })
                        .catch(error => {
                            console.log("Error: ", error)
                        })
        },
        notify(type, msg) {
            this.showAlert = true;
            this.alertType = type;
            this.alertMsg = msg;

            setTimeout( () => { this.showAlert = false; }, 3000);
        }
    }
}
</script>

<style scoped>
table {
    border-collapse: collapse;
}
</style>