<template>
  <div class="container" v-if="name">
      <h1>Edit</h1>
      <form @submit.prevent="update()" class="col-6 offset-3">
          <div class="form-group">
              <b-alert :show="showAlert" :variant="alertType" dismissible>{{ alertMessage }}</b-alert>
              <!-- Name -->
              <b-input size="sm" type="text" v-model="name"></b-input>
          </div>
          <b-button v-if="!submitLoading" type="submit" class="btn btn-sm btn-success">Submit</b-button>
          <b-button v-if="submitLoading" class="btn btn-sm btn-success" disabled><b-spinner type="grow" small></b-spinner> Submit</b-button>
        <p align="left">
            <router-link to="/firebase-demo">&#8592; back</router-link>
        </p>
      </form>
      
  </div>
</template>

<script>
import firebase from 'firebase/app';
import 'firebase/database'

export default {
    data() {
        return {
            submitLoading: false,
            showAlert: false,
            alertType: 'success',
            alertMessage: '',
            name: '',
        }
    },
    mounted() {
        this.loadUser()
    },
    methods: {
        loadUser() {
            var firestore = firebase.firestore();
            var id = this.$route.params.id;

            firestore.collection('users').doc(id).get()
                        .then( doc => {
                            if(doc.exists) {
                                this.name = doc.data().name;
                            } else {
                                this.$router.push('/NotFound');
                            }
                        })
                        .catch(error => {
                            console.log('Error: ',error);
                        })
        },
        update() {
            var firestore = firebase.firestore();
            var docRef = firestore.collection('users');
            var id = this.$route.params.id;

            this.submitLoading = true;

            docRef.doc(id).set({
                name: this.name,
            }).then( () => {
                this.notify('success', 'user successfully updated.');
                this.submitLoading = false;
            }).catch( error => {
                this.notify('danger', error);
                this.submitLoading = false;
            })
        },
        notify(type, msg) {
            this.showAlert = true;
            this.alertType = type;
            this.alertMessage = msg;

            // setTimeout( () => { this.showAlert = false}, 2500);
        }
    }
}
</script>

